import { request } from 'graphql-request'
import { Person } from './Models';

const ENDPOINT = 'https://112qaej5y9.execute-api.ap-southeast-2.amazonaws.com/dev/graphql';

function sendRequest<T>(query: string, base: string) {
    return new Promise<T>(resolve => {
        request(ENDPOINT, query)
            .then((data:any) => resolve(data[base]))
            .catch(err => {
                console.log(err);
                //reject(err);
            });
    });
}

export function getPersonId(name: string) {
    return new Promise<number>(resolve => sendRequest(`{
        searchPeople(query: "${name}") {
          id
        }
      }`, 'searchPeople').then((data:any) => resolve(parseInt(data[0].id, 10)))
    );
}

export function getPersonWithMovies(personId: number) {
    return sendRequest<Person>(`{
        person(id: "${personId}") {
          name
          appearsIn(limit: 100) {
            ... on Movie {
              id
              title
              releaseDate
              backdrop {
                medium
              }
              overview
              popularity
              poster {
                thumbnail
              }
              score
              votes
            }
          }
        }
      }
      `, 'person');
}

